html {
background-color:black;
background-image:
radial-gradient(white, rgba(255,255,255,.2) 2px, transparent 40px),
radial-gradient(white, rgba(255,255,255,.15) 1px, transparent 30px),
radial-gradient(white, rgba(255,255,255,.1) 2px, transparent 40px),
radial-gradient(rgba(255,255,255,.4), rgba(255,255,255,.1) 2px, transparent 30px);
background-size: 550px 550px, 350px 350px, 250px 250px, 150px 150px;
background-position: 0 0, 40px 60px, 130px 270px, 70px 100px;
color: lightgray;

}
input:focus,
select:focus,
textarea:focus,
button:focus,
summary:focus
 {
    outline: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}



.form{
    padding-top: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.form__input{
    border: none;
    background-color:transparent;
    border-bottom: 2px solid lightgray;
    flex-basis: 150px;
    text-align: center;
    margin: 20px;
    font-size: 20px;
    color: lightgray;
}


.form__btn{
flex-basis: 125px;
padding: 12px;
border-radius: 10px;
margin-right: 15px;
background-color: rgb(24, 65, 177);
border: none;
color: lightgray;
font-weight: bold;
cursor: pointer;
-webkit-transition: .2s;
transition: .2s;
}
.form__btn:hover{
background-color:rgb(34, 75, 187);
}

@media (orientation:landscape){
    .form{
        flex-wrap: nowrap;
    }
    .form__input{
    }
    .form__btn{
        flex-basis: 135px;
        padding: 12px;
    }
}

@media(min-width:1024px){
    .form__input{
        flex-basis: 160px;
        margin: 0 20px;
    }
}
@media(min-width:1600px){
    .form__input{
        flex-basis: 190px;
        margin: 0 30px;
    }
    .form__btn{
        flex-basis: 185px;
        padding: 15px;
    }
}
.info{
margin: 20px auto;
font-size: 18px;
width: 100%;
}

.info__icon{
    text-align: left;
    font-size: 32px;
}

.info__text{
    display: inline-block;
    width: 180px;
    text-align: center;
}

